#loginContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  height: 500px;
}

#title {
  display: block;
  width: 100%;
}

#loginButton {
  width: 200px;
  height: 50px;
  border-radius: 3px;
  border: 2px solid black;
  background: white;
  font-size: 15px;
  display: block;
  -webkit-transition: background 1s, color 0.5s;
  -o-transition: background 1s, color 0.5s;
  transition: background 1s, color 0.5s;
}


#loginButton:hover {
  background: black;
  color: white;
}



